import { useParticles } from "./hooks/useParticles";

type HeaderProps = {
  route: string;
};

const Header: React.FC<HeaderProps> = ({ route }) => {
  const { dispatch, collision } = useParticles();

  return (
    <header>
      <button
        onClick={() => dispatch({ type: "SET_PARTICLES_AMOUNT", value: 6 })}
      >
        {`${collision ? "Disable " : "Enable"} collision`}
      </button>
      <button
        onClick={() => dispatch({ type: "SET_PARTICLES_AMOUNT", value: 0 })}
      >
        Remove particles
      </button>
      <button
        onClick={() => dispatch({ type: "SET_PARTICLES_AMOUNT", value: 6 })}
      >
        Reset
      </button>
      <input
        type="number"
        max={100}
        defaultValue={6}
        placeholder="Max value 150"
        onChange={(e) => {
          const val = parseInt(e.target.value) as number;
          if (!isNaN(val)) {
            dispatch({
              type: "SET_PARTICLES_AMOUNT",
              value: val > 150 ? 150 : val,
            });
          }
        }}
      />
    </header>
  );
};
export default Header;
